<template>
  <div class="formtwo-container">
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="商品售价" prop="price">
        <el-input v-model="ruleForm.price"></el-input>
      </el-form-item>
      <el-form-item label="商品折扣价" prop="price_off">
        <el-input v-model="ruleForm.price_off"></el-input>
      </el-form-item>
      <el-form-item label="商品库存" prop="inventory">
        <el-input v-model="ruleForm.inventory"></el-input>
      </el-form-item>
      <el-form-item label="计量单位" prop="unit">
        <el-input v-model="ruleForm.unit"></el-input>
      </el-form-item>
      <el-form-item label="商品相册" prop="img">
        <el-upload
          width="100px"
          action="https://jsonplaceholder.typicode.com/posts/"
          list-type="picture-card"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
      </el-form-item>
      <el-form-item prop="status">
        <el-checkbox v-model="ruleForm.status">是否上架</el-checkbox>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="prv">上一步</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"
          >完成</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        price: "",
        price_off: "",
        inventory: "",
        unit: "",
        img: "",
        status: true,
      },
      rules: {
        price: [
          { required: true, message: "请输入商品售价", trigger: "blur" },
          {
            validator: (rule, value, callback) => {
              value = parseFloat(value);
              // console.log(isNaN(value))
              if (isNaN(value)) {
                callback(new Error("请输入数字"));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
        ],
        price_off: [
          { required: true, message: "请输入商品折扣价", trigger: "blur" },
          {
            validator: (rule, value, callback) => {
              value = parseFloat(value);
              const priceValue =
                this.ruleForm.price !== ""
                  ? parseFloat(this.ruleForm.price)
                  : 0;
              if (priceValue == 0) {
                callback(new Error("请填写原价"));
              } else if (value > priceValue) {
                callback(new Error("折扣不正确"));
              } else if (isNaN(value)) {
                callback(new Error("请输入数字"));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
        ],
        inventory: [
          { required: true, message: "请输入商品库存", trigger: "blur" },
          {
            validator: (rule, value, callback) => {
              value = parseFloat(value);
              if (isNaN(value)) {
                callback(new Error("请输入数字"));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
        ],
        unit: [
          { required: true, message: "请输入商品计量单位", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          //   alert("submit!");
          this.$emit("success", this.ruleForm);
        } else {
          //   console.log("error submit!!");
          return false;
        }
      });
    },
    prv() {
      this.$emit("prv", 0);
    },
  },
};
</script>

<style scoped lang="less">
.formtwo-container {
  width: 70%;
  margin-top: 30px;
  el-form {
    margin: 0 auto;
  }
  .el-form-item__content {
    width: 100px;
  }
}
</style>