<template>
  <div class="steps-container">
    <el-steps :active="active" finish-status="success">
      <el-step title="填写商品基本信息"></el-step>
      <el-step title="填写商品销售信息"></el-step>
    </el-steps>
    <keep-alive>
      <Fromone @next="next" v-if="active == 0" />
      <!-- <Fromtwo @prv="prv" @success="success" v-if="active == 1" /> -->
      <Fromtwo @prv="prv" @success="success" v-else />
    </keep-alive>
  </div>
</template>

<script>
import Fromone from "@/components/View/Formone.vue";
import Fromtwo from "@/components/View/Formtwo.vue";
import api from "@/api/product.js";
export default {
  components: {
    Fromone,
    Fromtwo,
  },
  data() {
    return {
      active: 0,
      addData: {},
    };
  },
  methods: {
    next(Formone) {
      this.active = 1;
      this.addData = Formone;
      // console.log(Formone)
    },
    prv() {
      this.active = 0;
    },
    success(Formtwo) {
      // console.log({...Formtwo})
      let obj = {
        title: this.addData.title,
        desc: this.addData.desc,
        category: parseInt(this.addData.category.one),
        c_items: [`${this.addData.category.two}`],
        tags: [`${this.addData.tags}`],
        price: parseInt(Formtwo.price),
        price_off: parseInt(Formtwo.price_off),
        unit: Formtwo.unit,
        status: Formtwo.status ? 1 : 0,
        images: [`${Formtwo.img}`],
        inventory: parseInt(Formtwo.inventory),
      };
      // console.log(obj)
      api.add(obj).then((res) => {
        // console.log(res)
        if (res.status == "success") {
          this.$router.push({ name: "productList" });
        }
      });
    },
  },
};
</script>

<style scoped lang="less">
.steps-container {
  width: 50%;
  margin: 30px auto;
}
</style>