<template>
  <div class="formone-container">
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item label="副标题" prop="title">
        <el-input v-model="ruleForm.title"></el-input>
      </el-form-item>
      <el-form-item label="商品描述" prop="desc">
        <el-input type="textarea" v-model="ruleForm.desc"></el-input>
      </el-form-item>
      <el-form-item label="商品类目" prop="category">
        <el-select
          v-model="ruleForm.category.one"
          placeholder=""
          @change="handleId(ruleForm.category.one)"
          clearable
        >
          <el-option
            :label="item.name"
            :value="item.id"
            v-for="item in data"
            :key="item.id"
          ></el-option>
        </el-select>
        <el-select v-model="ruleForm.category.two" placeholder="" clearable>
          <el-option
            :label="item"
            :value="item"
            v-for="item in c_items.c_items"
            :key="item"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="商品标签" prop="tags">
        <el-input v-model="ruleForm.tags"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"
          >下一步</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import api from "@/api/product.js";
export default {
  data() {
    return {
      ruleForm: {
        title: "",
        desc: "",
        category: {
          one: "",
          two: "",
        },
        tags: "",
      },
      rules: {
        title: [
          { required: true, message: "请输入商品副标题", trigger: "blur" },
        ],
        tags: [{ required: true, message: "请输入商品标签", trigger: "blur" }],
      },
      data: [],
      c_items: [],
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          //   alert("submit!");
          this.$emit("next", this.ruleForm);
          console.log()
        } else {
          //   console.log("error submit!!");
          return false;
        }
      });
    },
    handleId(id) {
      // console.log(id)
      this.c_items = this.data[id - 1];
      this.ruleForm.category.two = "";
      if (!this.c_items) {
        this.c_items = [];
      }
    },
  },
  created() {
    api.categoryList().then((res) => {
      // console.log(res.data.data)
      this.data = res.data.data;
    });
  },
};
</script>
<style scoped lang="less">
.formone-container {
  width: 70%;
  margin-top: 50px;
  el-form {
    margin: 0 auto;
  }
}
</style>